@import url(https://fonts.googleapis.com/css?family=Lato);
.setting {
    color: '#ffffff';
    background-color: '#044f62';
    border-bottom: 1px solid #F4AF1B;
    padding: 10px 15px;
    letter-spacing: 2.5px;
}

input {
    width: 100%;
}

.labels {
    display: flex;
    justify-content: space-between;

}

.label {
    width: 50%;
}

.label-left {
    min-width: 100px;
    text-align: left;
}

.label-right {
    min-width: 100px;
    text-align: right;
}

.range-field {
    text-align: center;
}

canvas {
    background-color: rgba(249, 249, 249, 0.5);
    width: 100%;
    margin: 20px 0;
    border: 10px double #00154F;
}

.save {
    display: inline-block;
    padding: 10px 5px 5px;
    background-color: #F2BC94;
    background-color: #00154F;
    color: #F2BC94;
    font-weight: bold;
    text-decoration: none;
    height: 24px;
    width: 80px;
    text-align: center;
    box-shadow: 2px 3px 5px 1px rgba(0, 0, 0, 0.2);

}

.save:hover {
    color: #00154F;
    background-color: #F2BC94;
}

input[type='file'] {
    padding: 8px;
    margin: 10px 35px;
    color: #00154F;
    width: 50%;
    border: 5px solid #00154F;
    /*border-radius: 3px;*/
}

.header {
    width: 100%;
    text-align: center;
    color: rgb(255, 255, 255);
    background-color: #00154F;
    position: fixed;
    top: 0px;
    margin: 0px 0px 1px;
    padding: 5px;
    letter-spacing: 5px;
    box-shadow: rgba(0, 0, 0, 0.2) 2px 3px 5px 1px;
    z-index: 1000;
}

h1 {
    color: #F2BC94
}

@media (max-width: 767px) {
    .header {
        position: static;
        padding: 0;
    }
    .header h1 {
        margin: 0;
    }
}

html, body {
    box-sizing: border-box;
    font-size: 14px;
    background-color: #eee;
    letter-spacing: 1.5px;
}

.wrapper {
    display: flex;
    justify-content: flex-start;
    margin-top: 80px;
}
@media (max-width: 767px) {
    .wrapper {
        margin-bottom: 80px;
    }
}

.sidebar {
    width: 40%;
    color: #F4AF1B;
    background-color: #00154F;
    box-shadow: 0px 0px 10px #000;

}

@media (max-width: 767px) {
    .sidebar {
        width: 100%;
        position: fixed;
        left: 0;
        bottom: 0;
        display: inline-flex;
        overflow-x: scroll;
    }
}

/*@media (min-width: 768px) {
    .sidebar {
        display: inline-block;
        width: 40%;
    }
}*/

@media (min-width: 992px) {
    .sidebar {
        display: inline-block;
        width: 30%;
    }
}

@media (min-width: 1200px) {
    .sidebar {
        display: inline-block;
        width: 25%;
        /*margin-right: 50px;*/
    }
}


.main-area {
    margin: 0 30px;
}

.App {
  font-family: 'Lato', sans-serif;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

